import { Component, Input } from '@angular/core'
import {
  IonItem,
  IonLabel,
  IonButton,
  IonIcon,
  IonCol,
  IonGrid,
  IonNote,
  IonRow,
  IonLoading,
  IonText,
  IonToast,
  IonSpinner,
} from '@ionic/angular/standalone'
import { CommonModule } from '@angular/common'
import { addIcons } from 'ionicons'
import {
  bicycle,
  batteryFull,
  batteryHalf,
  batteryDead,
  batteryCharging,
  lockOpen,
  thumbsUp,
} from 'ionicons/icons'
import { RecommandationComponent } from '../recommandation/recommandation.component'
import { TranslateModule } from '@ngx-translate/core'
import { ListVehicule200ResponseAllOfDataInner } from 'src/api-cyclist'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import {
  AccountService,
  cyclistState,
} from 'src/app/services/api-wrappers/account.service'
import { effect } from '@angular/core'

@Component({
  selector: 'app-vehicle',
  templateUrl: './vehicle.component.html',
  styleUrls: ['./vehicle.component.scss'],
  imports: [
    IonItem,
    IonButton,
    IonIcon,
    IonNote,
    RecommandationComponent,
    TranslateModule,
    CommonModule,
  ],
})
export class VehicleComponent {
  @Input() vehicle!: ListVehicule200ResponseAllOfDataInner
  cyclistState: cyclistState = {
    state: 'unknown',
    blocked_reasons: ['Unknown account state'],
  }

  constructor(
    private rentingService: RentingService,
    public accountService: AccountService
  ) {
    addIcons({
      bicycle,
      batteryFull,
      batteryHalf,
      batteryDead,
      batteryCharging,
      lockOpen,
      thumbsUp,
    })
    effect(() => {
      this.cyclistState = this.accountService.cyclistState()
    })
  }

  getBatteryIcon(): string {
    const percent = this.vehicle.battery_vae?.percent || 0
    if (percent > 70) return 'battery-full'
    if (percent > 30) return 'battery-half'
    return 'battery-dead'
  }
  async rent() {
    await this.rentingService.startRenting(this.vehicle)
  }
}
