import { Component, OnInit } from '@angular/core'
import { Observable } from 'rxjs'
import {
  IonList,
  IonItem,
  IonLabel,
  IonIcon,
  IonButton,
  IonSpinner,
  IonSkeletonText,
  IonText,
  IonNote,
} from '@ionic/angular/standalone'
import { CommonModule } from '@angular/common'
import { VehicleComponent } from '../../vehicle/vehicle.component'
import {
  ListStation200Response,
  ListStation200ResponseAllOfDataInner,
} from 'src/api-cyclist'
import { locationOutline } from 'ionicons/icons'
import { addIcons } from 'ionicons'
import { TranslateModule } from '@ngx-translate/core'
import {
  GeolocationService,
  GeolocationState,
} from 'src/app/services/utils/geolocation.service'
import { StationWrapperService } from 'src/app/services/api-wrappers/stations.service'

type LoadingState =
  | { state: 'LOADING' }
  | { state: 'NO_NEARBY_STATIONS_OR_VEHICLES' }
  | { state: 'ERROR'; error: Error }
  | {
      state: 'LOADING_IN_PROGRESS'
      stations: ListStation200ResponseAllOfDataInner[]
    }
  | {
      state: 'SUCCESS'
      stations: ListStation200ResponseAllOfDataInner[]
    }

@Component({
  selector: 'app-nearest-vehicles',
  templateUrl: './nearest-vehicles.component.html',
  styleUrls: ['./nearest-vehicles.component.scss'],
  imports: [
    IonList,
    IonItem,
    IonSkeletonText,
    IonNote,
    IonButton,
    IonText,
    IonIcon,
    CommonModule,
    TranslateModule,
    VehicleComponent,
  ],
})
export class NearestVehiclesComponent implements OnInit {
  stations$: Observable<ListStation200Response>
  loadingState: LoadingState = {
    state: 'LOADING',
  }
  geolocState: GeolocationState

  constructor(
    public geolocationService: GeolocationService,
    private stationWrapperService: StationWrapperService
  ) {
    this.geolocationService.geolocationState.subscribe((state) => {
      this.geolocState = state
    })
    addIcons({ locationOutline })
  }
  ngOnInit(): void {
    this.loadStations()
  }
  private async loadStations() {
    this.loadingState = { state: 'LOADING' }
    if (
      this.geolocState.state === 'live_position' ||
      this.geolocState.state === 'unique_position'
    ) {
      this.stationWrapperService
        .getNearbyStations(this.geolocState.position.coords, 1000, true)
        .then((stations) => {
          if (stations.length > 0) {
            this.loadingState = { state: 'SUCCESS', stations }
          } else {
            this.loadingState = { state: 'NO_NEARBY_STATIONS_OR_VEHICLES' }
          }
        })
        .catch((error) => {
          this.loadingState = { state: 'ERROR', error }
        })
    } else {
      this.loadingState = { state: 'NO_NEARBY_STATIONS_OR_VEHICLES' }
    }
  }

  calculateDistance(station: ListStation200ResponseAllOfDataInner) {
    if (
      this.geolocState.state === 'live_position' ||
      this.geolocState.state === 'unique_position'
    ) {
      const distance = this.geolocationService.calculateDistance(
        this.geolocState.position.coords,
        {
          latitude: Number(station.position?.latitude),
          longitude: Number(station.position?.longitude),
        }
      )

      return distance
    }
    return 0
  }
}
